    <div class="product-content" v-loading="loading">
      <div class="product-content-content">
        <h3 class="title" style="margin: 20px auto">苗医药的历史、传承及发展现状</h3>
        <h4 style="margin: 0px 0px 10px auto">——以孙百近医师的诊疗为例</h4>
        <img :src="require('../assets/img/history.jpg')">
        <p class="product-content">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;湖南湘西是我国苗族主要聚集地，古书记载的崇山是在湘西苗族土家族自治州管辖境内，这里有得天独厚的自然环境，山峦重叠、川流纵横、气候温和、雨量充沛，因此动植物物种、矿务资源极为丰富。湖南湘西是我国苗医苗药发源地，也是我国珍贵中草药材产区之一。

        <br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;数千年来苗族人民在此辛勤耕作，由于山高崇险，路途隔阻，基本与外界没有往来，在这样生存环境，山区苗族人民在长期与疾病作斗争漫长历史中，积累了丰富的理论知识，独特的治病技术与临床经验，苗族民间流行的“千年苗医，万年苗药”之说，足见苗医药治病技术源远流长，成为我国医药领域上的一支奇葩，因此湘西苗医苗药素有崇山一绝之称。

        <br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;湘西苗药主要是指苗族人民世代相传的民间草药，是我国苗族地区传统医药百花园中的一朵奇葩。《凤凰厅志》曾经记载“苗地多产药饵……非方书所载，或吞或敷，奏效甚捷”。草药性质较烈，尤其鲜药，成分保全，药效比较好，时间短而速，治愈后副作用小，因此苗医都喜用鲜药。这些草药，对于比较顽固的中老年腰腿疼痛、腰椎间盘突出、颈椎病、风湿关节痛、类风湿、中风、偏瘫后遗症康复等顽固性病症的治疗都有显著的疗效。

        <br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;历史上有许多关于苗医药疗效的故事。例如，民国初年，内阁总理熊希龄曾亲自目睹了苗医治疗枪伤神效，并带苗医师入京；贺龙在长征时多次强调要带苗医师随军前进；2001年4月7日，国家总理朱镕基来花垣县视察时曾说，他于1945年在花垣国立高中读书时患的霍乱病，就是苗医师用传统的苗医药治愈的（可参见《南方日报》2003年3月6日第4版）。

        <br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;近现代以来，如今的湘西苗药治病医术，传承了苗药文化，是根据湘西苗族人民千万年古老而传统的治病理论和养生医术，依靠湘西大山独有的中草药，集苗医药专业技术，更是凝聚了国家著名苗医大师龙玉六，田兴秀、龙玉年创新医术。湘西苗族著名苗医大师龙玉六，利用湘西苗族人民几千年古老的治病技术，自1959年发明了苗药蒸汽疗法以来，通过新一代苗药传承人不断总结经验，结合现代科学理论，历经几代人艰苦摸索，孙百近、丁新中在湘西苗药蒸汽治疗法师传基础上加以创新，现发明了一种新一代苗药薰蒸治疗法，能快速治好十几种疑难杂症，特别是针对腰腿关节疼痛、中风后遗症的康复。新一代的苗药蒸汽治疗法，已得到国家非物质文化遗产司、中国民间中医药研究开发协会、中国民族卫生协会培训部等国家领导高度重视。

        <br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;除了苗药熏蒸这一治疗手段，传承人孙百近医师进一步承袭了湘西苗医药祖传医术，并形成了一套具有特殊的苗医药体系。例如，独特手法整脊正骨复位，推拿针灸、苗药熏蒸、中草药外敷等治疗方法，这些治疗方法对股骨头坏死、中风后遗症康复、肩颈腰椎间盘突出、肩颈腰肌劳损、顽固性腰腿疼痛、类风湿性关节肿痛变形、痛风、风湿关节疼痛等病症治疗有显著疗效，其中，孙百近大师特别对股骨头缺血坏死、三高脑血栓中风抢救与后遗症康复有丰富的临床经验。

        <br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;苗医药传承人孙百近大师是高级康复医师、民族医师、国家苗医苗药非物质文化遗产传承人。已获医药国家发明专利三项，实用新型专利二项，注册医药商标一项，发表医学康复治疗论文五篇。现任中国民族卫生协会培训部腰腿疼痛、类风湿中风后遗症康复科研基地主任，中国民族卫生协会专家委员，中国民间中医药研究开发协会皮肤专家委员会副主任、委员。并多次受邀湖南卫视金鹰频道、丁点真相、长沙政法频道、湖南公共频道等的采访和报道，讲述关于“神秘而古老的苗医药治病医术”。
        </p>
